<template>
  <div class="resize-demo">
    <div class="ori-img">
      <div>
        <el-upload
          class="upload-demo"
          action="https://jsonplaceholder.typicode.com/posts/"
          :auto-upload="false"
          accept="image/*"
          :show-file-list="false"
          :on-change="uploadFile"
          >
          <el-button size="small" type="primary">点击上传</el-button>
        </el-upload>
        <span v-if="imageUrl">原始图片 width:{{oriDimension.width}} height:{{oriDimension.height}} size:{{file?parseInt(file.size/1024)+'kb':''}}</span>
      </div>
      <div v-if="imageUrl" class="img-container">
        <img :src="imageUrl" />
      </div>
    </div>
    <div class="target-params" v-if="imageUrl">
      <el-button size="small" type="primary" @click="resizeImage">压缩</el-button>
      width:
      <el-input v-model.trim="target.width"></el-input>
      height:
      <el-input v-model.trim="target.height"></el-input>
      quality(0.x):
      <el-input v-model.trim="target.quality"></el-input>
    </div>
    <div v-if="resizedUrl" class="resize-img">
      <div class="img-container">
        <img :src="resizedUrl" />
      </div>
      <br>
      <el-button size="small" type="primary" @click="saveToLocal">保存到本地</el-button>
      <span>压缩后大小: {{resizedFile?parseInt(resizedFile.size/1024)+'kb':''}}</span>
    </div>
  </div>
</template>
<script>
import Vue from 'vue';
import { Loading } from 'element-ui';

export default {
  data() {
    return {
      imageUrl: '',
      file: null,
      resizedUrl: '',
      resizedFile: null,
      oriDimension: {
        width: '',
        height: '',
      },
      target: {
        width: '',
        height: '',
        quality: '',
      },
      loadingInstance: null,
    }
  },
  methods: {
    uploadFile(file) {
      console.log(file);
      this.imageUrl = file.url;
      this.file = file.raw;
      Vue.prototype.$imgDimension(file.raw).then((data) => {
        this.oriDimension = data;
      });
    },
    validParams() {
      if (parseInt(this.target.width) === NaN || parseInt(this.target.width) < 0) {
        this.$message.error('请输入合法width');
        return false;
      }
      if (parseInt(this.target.height) === NaN || parseInt(this.target.heigh) < 0) {
        this.$message.error('请输入合法height');
        return false;
      }
      return true;
    },
    resizeImage(image) {
      if (!this.validParams()) return;
      let obj = {
        width: this.target.width,
        height: this.target.height,
        quality: this.target.quality,
      }
      this.loadingInstance = Loading.service(
        { 
          fullscreen: true,
          text: '压缩中',
        }
      );
      Vue.prototype.$resizeImage(this.file, obj).then((data) => {
        this.resizedUrl = data.dataurl;
        this.resizedFile = data.blob;
        this.loadingInstance.close();
      }).catch((err) => {
        this.loadingInstance.close();
        this.$message.error(err.message);
      })
    },
    saveToLocal() {
      let a = document.createElement('a');
      let event = new MouseEvent('click');
      a.download = this.resizedFile.name;
      a.href = this.resizedUrl;
      a.dispatchEvent(event);
    }
  }
};
</script>
<style lang="scss">
  .resize-demo {
    .ori-img {
      .upload-demo {
        display: inline-block;
      }
      .img-container {
        display: inline-block;
        max-width: 800px;
        max-height: 500px;
        margin-top: 20px;
        overflow: auto;
      }
    }
    .target-params {
      .el-input {
        width: 100px;
      }
    }
    .resize-img {
      .img-container {
        display: inline-block;
        max-width: 800px;
        max-height: 500px;
        margin-top: 20px;
        overflow: auto;
      }
    }
  }
</style>





